.diaporama-trigger {
    width: 100%;
    height: auto;
    position: relative;
    overflow: hidden;

    &__button {
        position: absolute;
        top: 50%; 
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 300;
    }
  
    &__slide {
      position: relative;
      width: 100%;
    }
  
    &__arrows {
      @include circled-icon(8.5vw);
      background: $yellow;
      color: $black;
      top: -2vw;
      font-size: 1.5rem;
  
      @include breakpoint(tabletOnly) {
        @include circled-icon(4.5vw);
        font-size: 1.3rem;
        top: -1.8vw;
      }
  
      @include breakpoint(desktopOnly) {
        @include circled-icon(2vw);
        font-size: 1.1rem;
        top: 12vw;
      }

    }
  
    &__thumbs {
      width: calcVw(580, 1700);
      position: absolute;
      margin: 0 auto;
      left: 0;
      right: 0;
      bottom: 3.7vw; 
      z-index: 300;
  
      @include breakpoint(phoneAndTablet) {
        display: none;

      }

    }
    
    &__thumb {
        position: relative;
        display: inline-block;
        cursor: pointer;
        outline: none;
        max-width: calcVw(100, 1700);

        &:nth-child(even) {
            margin: 0 calcVw(20, 1700);
        }

    }

    &__footnote {
        @extend .button--caption;
        right: 0;
        background-color: $white;
        
        span {
            color: $black;
            @include fontSize(20,11,11);
            @include letterSpacing(18,18,18);
            @include lineHeight(40,40,40);
            padding: 0 calcRem(13) 0 calcRem(10);
        }
    }
}
  
